
$bp-largest: 80em;
$bp-large: 56.25em;
$bp-md: 43.75em;
$bp-sm: 35em;

.shelf{
    &-container{
        padding: 6rem 6rem;
        background-image: linear-gradient(to bottom, #e9defa 0%, #ffffff 100%);

        @media only screen and (max-width: $bp-large) {
            background-image: none;
            background-color:  white; 
        }

    }
   
    &-wrapper{
    display: flex;
    flex-direction: column;
    }
}



.shelf-top{

    

    flex: 1;
    padding-left: 1rem;
    display: flex;
    justify-content: space-between;
}

.shelf-dropdown {
  
    &-button {
      @media only screen and (max-width: $bp-sm) {
        width: 14rem;

      }

      width: 18rem;
      height: 3.6rem;
      border-radius: 0.6rem;
      background-color: rgba(255, 255, 255, 0.986);

      @media only screen and (max-width: $bp-large) {
        background-color:  rgb(221, 221, 221); 
    }

      color: black;
      border: 0;
      transition: all 0.2s;
      font-family: inherit; /* Important for vertical align on mobile phones */
      box-shadow: 0px 0px 1rem 0rem rgba(0, 0, 0, 0.082);
      outline: none;
      z-index: 2;
      position: relative;
      text-align: left;
      padding-left: 1rem;
  
      &:hover {
        @media only screen and (max-width: $bp-large) {
            background-color:  rgb(199, 199, 199); 
        }
        background-color: #f1f1f1;
      }
  
      &::after {
        position: absolute;
        right: 1.1rem;
        top: 1.6rem;
        content: "";
        width: 0px;
        height: 0px;
        border-style: solid;
        border-width: 0.5rem 0.4rem 0 0.4rem;
        border-color: rgb(31, 31, 31) transparent transparent transparent;
        border-radius: 0.4rem;
      }
    }
  }

.shelf{
    margin-top: .5rem;
}


.choice-active {
    font-weight: 700;
  }

.show {
    display: block;
  }


  .shelf-empty{

      font-size: 3rem;
      font-weight: 700;
      color: rgb(173, 173, 173);
      grid-column: 1/-1;
      padding-top: 15rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }